<template>  
    <router-view></router-view>  
</template>

<style lang="less">
	@import './style/main.less';
</style>
<script>
	export default {
		created () {
			let token = this.$route.query.token
			if(token){
				this.tokenLogin(token)
			}
			this.$router.push('/base')
		},
		methods:{
			tokenLogin(token){
				this.$http.post('tklogin',
		          {token:token}
		          )
		        .then(response=>{
		          Auth.setLocalLogin(response.body.data)
		          this.$router.push({ path: '/' })
		        },response=>{
	              this.loading = false
	              let msg = response.body.message
	              let msg_map = {
	                'captcha_error':'��֤�����',
	                'password_is_wrong':'�������',
	                'account_does_not_exists':'�˺Ų�����',
	                'user_is_disabled':'�˺ű�����',
	                'client_time_not_correct':'���ĵ���ϵͳ���ڲ���,���Ƚ���������������ȷ!'
	              }
	              msg = msg_map[msg]?msg_map[msg]:msg;
	              if(response.body.message == 'captcha_error'){
	                  this.focus('captcha')
	              }else if(response.body.message == 'password_is_wrong'){
	                  this.focus('password') 
	              }else if(response.body.message == 'account_does_not_exists'){
	                  this.focus('account')
	              }
	              //this.refreshCaptcha()
	              this.$Message.error(msg)
		        })
			}
		}
	}
</script>



